import React , {useState} from 'react';
import {useParams , useHistory} from 'react-router-dom';


function Detail(props){
    let {id}  = useParams();
    let newId = props.shoes.find(function(product){
        return product.id == id
    });
    return(
        <div className="container">
            <div className="row">
                <div className="col-md-8">
                    <img src={`https://codingapple1.github.io/shop/shoes${Number(id)+1}.jpg`} width='100%'/>
                    
                </div>
                <div className="col-md-4 mt-4">
                    <h4 className="pt-5">{newId.title}</h4>
                    <p>{newId.content}</p>
                    <p>{newId.price}</p>
                    <button type="button" class="btn btn-primary btn-margin">주문하기</button>
                    <button type="button" class="btn btn-primary btn-margin">뒤로가기</button>
                </div>
            </div>
           

            
        </div>
    );
}

export default Detail;