import React, {useState} from 'react';
import {Link, useParams} from 'react-router-dom';

function Product(props){
    return(
        <div className="container product-container" >
            <div className='row'>
                {
                    props.shoes.map(function(a ,j) {
                        return(
                            <div className="col-md-4">
                                <img src={`https://codingapple1.github.io/shop/shoes${j+1}.jpg`} width='100%'/>
                                <h1>{a.title}</h1>
                                <p>{a.content}</p>
                                <p>{a.price}</p>
                                <Link to={`/product/${a.id}`}><button type='button' className="btn btn-primary">상품 상세 더보기</button></Link>
                            </div>
                        )
                    })
                }
                   
                
            </div>
        </div>
    )
}

export default Product;